/* colours */
$backgroundColor: #e9e9e9;
$bodyColor: #000000;
$linkColor: #f95527;

/* spacing */
$desktopWidth: 1380;
$largeSpace: 60px;
$mediumSpace: 30px;
$smallSpace: 15px;

/* fonts */
$bodyFont: "sohne", arial, helvetica, sans-serif;
$bodyDesktop: 24px;
$bodyMobile: 20px;

@font-face {
  font-family: "sohne";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/soehne-web-buch.woff") format("woff");
  src: url("../fonts/soehne-web-buch.woff2") format("woff2");
}

@font-face {
  font-family: "sohne";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/soehne-web-dreiviertelfett.woff") format("woff");
  src: url("../fonts/soehne-web-dreiviertelfett.woff2") format("woff2");
}

/*body*/

body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: "sohne";
  font-size: $bodyDesktop;
  line-height: 1.2;
}


/*text styles*/
h1 {
  font-family: "sohne";
  font-weight: normal;
  font-size: 3.5em;
  line-height: 1;
}

a {
  color: $linkColor;
  font-style: none;
  text-decoration: none;
}

p {
  font-size: 1em;
}

a:hover {
  color: $linkColor;
  font-style: none;
  text-decoration: underline;
}


a:visited {
  font-style: none;
}

/* navigation styles */

.nav-group{
  display: flex;
  flex-direction: row;
}

.nav-group-title{
  font-size: 1.1em;
  font-weight: bold;
  padding: 0 0 6px 0;
}

.nav-group-count{
  font-size: .6em;
  font-weight: normal;
  padding-left: 4px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-bottom: $mediumSpace;
}

li {
  padding: 0 0 5px 0;
}

.list {
  display: flex;
  flex-direction: row;
}

li:first-child .arrow {
  background: url(../images/bullet-first.svg) no-repeat left top;
  margin: -2px 0 0 0;
  padding: 0 22px 0 0;
}

li .arrow {
  background: url(../images/bullet-second.svg) no-repeat left top;
  margin: -20px 0 0 0;
  padding: 0 22px 0 0;
}

