
.site-wrapper{
    display: flex;
    max-width: $desktopWidth;
    flex-direction: row;
    height: 100vh;
    margin: auto;
}

.left {
    height: inherit;
    min-width: 360px;
    overflow: scroll;
}

.right {
    height: inherit;
    overflow: scroll;
}

nav {
    margin: 60px 0 50px $largeSpace;
}

.content {
    margin: 50px $largeSpace 50px $largeSpace;
}

.paragraph-width {
    max-width: 700px;
}

@media screen and (max-width: 980px){

    nav {
        margin: 50px 0 50px $mediumSpace;
    }
    
    .content {
        margin: 40px $mediumSpace 50px $mediumSpace;
    }

    h1 {
        font-size: 3em;
    }
}

@media screen and (max-width: 880px){

    h1{
        font-size: 1.8em;
    }

    .site-wrapper {
        display: flex;
        flex-direction: column;
        height: auto;
    }

    nav {
        margin: 40px 0 50px $smallSpace;
    }
    
    .content {
        margin: 50px $smallSpace 50px $smallSpace;
    }


}